import React, { Component } from "react";

import "./index.scss";

import Head from "&/components/head";
import ShopCar from "&/mobx/shopcar";
import { Button, Checkbox } from "antd-mobile"
import { observer } from "mobx-react"
console.log(ShopCar);
@observer
class Cart extends Component {


    componentDidMount() {
        if (!ShopCar.carList.length > 0) {
            ShopCar.getCarList();
        }
    }

    checkOne = (e) => {

        var checked = e.target.checked;
        var goodId = e.target.goodId;
        console.log(goodId);
        ShopCar.changeCheckOne(checked, goodId);
    }

    checkAll = (e) => {
        console.log(e.target.checked);
        ShopCar.changeQuan(e.target.checked)
    }

    reduce = (goodId, count) => {
        if (count > 1) {
            ShopCar.changeOneCount(goodId, false);
        }
    }

    add = (goodId, count) => {
        if (count < 100) {
            ShopCar.changeOneCount(goodId, true);
        }
    }

    changeCount = (goodId, v) => {
        var count = v.target.value;
        if (count > 1 && count < 100) {
            ShopCar.changeCountByGoodId(goodId, count * 1)
        }
    }

    delSelect = () => {
        ShopCar.delGoodBySelect();
    }


    render() {
        const {
            carList,
            quan,
            total,
            checkNum
        } = ShopCar;
        return (
            <div className="cart">
                <Head title="购物车" location={this.props.location}></Head>
                <div style={{ display: sessionStorage.token ? 'none' : 'block' }} >
                    <Button type="primary" > 你尚未登陆,请马上登录  </Button>
                </div>
                <div style={{ display: !sessionStorage.token ? 'none' : 'block' }}>
                    <div style={{ display: carList.length > 0 ? 'none' : 'block' }}>
                        <h2> 你 还买购买任何商品,购物车空空如也 , 请马上去购买 </h2>
                    </div>

                    <div style={{ display: !carList.length > 0 ? 'none' : 'block' }}  >
                        <div id="carDiv">
                            <div id="tbody">
                                <div style={{height:40,width:"100%"}}><Checkbox style={{ width: '4%', float: 'left', marginLeft: '2%', lineHeight: '40px', }} onClick={this.checkAll} checked={quan} ></Checkbox><span  style={{ width: '30%', float: 'left', marginLeft: '4%', lineHeight: '40px', fontSize:13}}>百丽优购</span></div>
                                {
                                    carList && carList.map((i, index) => {
                                        return (
                                            i.good && <ul className="tr" key={index} id={i._id}>
                                                <li style={{ width: "11%" }}>
                                                    <Checkbox
                                                        checked={i.checked}
                                                        goodId={i.goodId}
                                                        onChange={this.checkOne} >
                                                    </Checkbox>
                                                </li>
                                                <li style={{ width: "24%" }}><img src={i.good.url} /></li>
                                                <li style={{ width: "38%" }}>
                                                    <p className="msg" style={{ lineHeight: "18px", marginTop: '16px', fontSize: 14 }}>{i.good.name}</p>
                                                    <p style={{ lineHeight: "18px", marginTop: '16px', marginLeft: '36px' ,display:"flex",justifyContent:"spaceAround"}}>
                                                        <span style={{ display:"block" ,width:20,border:"1px solid #ccc", }} onClick={() => this.reduce(i.goodId, i.count)}>-</span>
                                                        <input goodsid={i.goodId} type="text" value={i.count} onChange={(v) => { this.changeCount(i.goodId, v) }} style={{ width: "30px", fontSize: 14, textAlign: 'center', background: 'none', border: '0',borderTop:"1px solid #ccc",borderBottom:"1px solid #ccc" }} />
                                                        <span style={{ display:"block" ,width:20,  border:"1px solid #ccc" }} onClick={() => this.add(i.goodId, i.count)}>+</span>
                                                    </p>
                                                </li>
                                                <li style={{ width: "26%" }}>
                                                    <p style={{ lineHeight: "18px", marginTop: '16px',marginLeft: '25px' }}>￥{i.good.newprice}</p>
                                                    <p onClick={this.delSelect} style={{ marginTop: '36px', lineHeight: '20px', fontSize: '14px', color: "red" }} ><img style={{width:20,height:20,paddingLeft:55}} src="http://m.yougou.com/images/del.png" alt=""/></p>
                                                </li>
                                            </ul>
                                        )
                                    })
                                }
                            </div>
                            <div className="carFoot">
                                <Checkbox style={{ width: '4%', float: 'left', marginLeft: '2%', lineHeight: '40px', }} onClick={this.checkAll} checked={quan} ></Checkbox>
                                <p  style={{ width: '24%', lineHeight: '40px', fontSize: '14px', color: "red", marginLeft: '4%' }} >全部</p>
                                <p style={{ width: '36%', lineHeight: '40px', color: "#000" ,fontSize: 10 ,marginLeft: '4%'}}>
                                    总计:<span style={{ width: '100px', color: "red", fontWeight: 700, fontSize: 14 }}> ￥ {total} </span>(不含运费)
                                </p>
                                <p style={{ backgroundColor: 'red', width: "26%", fontSize: 14, lineHeight: '40px', textAlign: 'center', color: "#fff", letterSpacing: '2px' }} >去结算 ({checkNum})</p>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default Cart